@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.grid{
   display: grid;
   grid-template-columns: repeat(1fr ,4);
   grid-gap: 10px;
   gap: 10px;
}
.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 80px;
    background-color: #3B34AB;
    padding: 30px 40px;
    overflow: none;
    display: flex;
    align-items: center;
    z-index: 100;
}
.nav-logo{
   color: #fff;
   font-size: 30px;
   cursor: pointer;
   padding-right: 60px;
   margin-bottom: 12px;
}
.navmenu{
    display: flex;
    z-index: 90;
    justify-content: space-between;
    vertical-align: baseline;
}
.navmenu .navlink{
    font-family: 'Montserrat',sans-serif;
    font-size: 17px;
    font-weight: 600;
    color: #fff;
    margin: 30px;
    cursor: pointer;
    transition: all .4s;
}

.btn{
    display: inline-block;
    border: 3px solid #222;
    background-color:#0f1124;
    border-radius: 7px;
    height: 55px;
    color: #4bd2b2;
    text-decoration: none;
    text-align: center;
    font-size: 20px;
    width: 170px;
    justify-self: flex-end;
    margin: 20px; 
    margin-right: 0px;
    margin-left: 70px;
}
.text-field{
    color: white;
}
@media (max-width : 1100px) {
    .navmenu .navlink{
        display: none;
    }
    .btn{
        margin-top: 8px;
    }
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    list-style: none;
}
.footer{
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
}
img{
    display: block;
    background: none;
}
.footer h3{
    margin: 10px;
    text-align: center;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 29px;
    letter-spacing: 0em;
}
.footer .img{
    height: 71px;
    width: 232px;
    border-radius: 5px;
}
.footer .img1{
    height: 60.86104965209961px;
    width: 216.26943969726562px;
    border-radius: 10px;
    border: none;
    background-size: cover;
}

*{
    box-sizing: border-box;
    line-height: 1.2em;
}
.main-container{
    display: block;
    max-height: 1200px;
    min-height: 600px;
    height: 100%;
    padding-top: 100px;
    background: #fff;  /* fallback for old browsers */
    /* background: -webkit-linear-gradient(to right, #243B55, #141E30);  
    background: linear-gradient(to right, #243B55, #141E30);
 */ } 
.container{
    display: flex;
    margin: 30px;
}
.container .text{
    margin: 80px;
    margin-top: 90px;
}
.container h1{
    color:#3B34AB;
    font-size: 3.1rem;
    margin-bottom: 12px;
}
.container h3{
    color:#000000;
    font-size: 2rem;
    margin-bottom: 7px;
}
.container p{
    color: black;
    font-size: 1.2rem;
    margin-bottom: 25px;
}
.container .btn{
    background-color: #3B34AB;
    color: #fff;
    width: 240px;
    height: 57px;
    border: none;
    margin-left: 0;
}

/*stats*/
.stats{
  position: relative;
  padding: 25px;
  max-height: 900px;
  min-height: 300px;
  line-height: 2.3rem;
}
.stats-container{
    display: flex;
}
.stats h1{
    color: #3B34AB;
    margin: 0 30%;
    font-size: 2.4rem;
    font-weight: 500;
}
.stats h2{
    margin: 5px 0;
    font-size: 1.5rem;
    font-weight: 500;
}
.stats p{
    margin: 9px 0;
    font-size: 1rem;
    font-weight: 400;
    line-height: 2rem;
}
.stats-data{
    margin: 50px;
}
.stats img{
    width: 370px;
    height: 217px;
}
.stats-container .btn{
    width: 145px;
    height: 58px;
    border: none;
    border-radius: 5px;
    background-color: rgba(59, 52, 171, 0.85);
    color: #fff;
    margin-left: 0;
}
.options .container-offer{
    text-align: center;
    margin-left: 30px;
    padding: 40px;
}
.options p{
    color: #222;
    font-size: 1.2rem;
    text-align: center;
    margin: 5px 0;
    margin-right: 40px;
}
.options .offers{
    color: #3B34AB;
    margin: 0 38%;
    font-size: 2.4rem;
    font-weight: 600;
}
.options img{
    width: 330px;
    height: 281px;
}
.features img{
    width: 129px;
    height: 99px;
    margin: 10px;
    margin-left: 0;
}
.features{
    margin-bottom: 90px;
}
.features p{
    color: #222;
    font-size: 1.2rem;
}
.features .offers{
    color: #3B34AB;
    margin: 10px 38%;
    font-size: 2.4rem;
    font-weight: 700;
}
.features .container-offer{
    margin: 8px;
}
.features .shadow-box{
    display: block;
    box-shadow: 5px 4px 15px 4px #00000040;
    height: 296px;
    width: 377px;
    padding: 12px;
    margin: 33px;
    border-radius: 12px;
}
.features .shadow-box h3{
    margin: 15px 0;
}
.features .shadow-box p{
    margin: 19px 0;
}
@media (max-width : 1000px) {
    .main-container{
        padding-top: 70px;
    }
    .container h1{
        font-size: 2rem;
    }
    .container p{
        margin-bottom: 10px;
    }
    .container{
        display: block;
        text-align: center;
    }
    .container img{
        width: 500px;
    }
    .stats-container{
        display: block;
    }
    .stats h1{
       font-size: 2.2rem;
       justify-items: center;
    }
    .container .text1 h1{
        font-size: 2.3rem;
    }
    .container .text1 p{
        font-size: 1.3em;
        line-height: 2.6rem;
        margin-top:1px;
    }
    .details img{
       width: 400px;
       height: 400px;
       justify-self: center;
    }
}
